<!--suppress HtmlUnknownTag -->
<template>
  <v-alert v-if="!backendPresent" type="warning">

    This page requires a backend server, but you are on the serverless domain.
    {{ beforeHref }}
    <a :href="href" style="color: #ca007a" >{{ href }}</a>
    {{ afterHref }}.

    <v-btn v-if="countdownGoing"
           @click="seconds = -1">Cancel</v-btn>
  </v-alert>
</template>

<script>
  export default {
    name: 'BackendRequired',

    props: ['backendPresent'],

    data: () => ({
      seconds: 10
    }),

    computed: {

      countdownGoing () {
        return this.seconds >= 0;
      },

      beforeHref () {
        return this.countdownGoing
          && 'You will be redirected to'
          || 'Please go to';
      },

      href () {
        return location.href
          .replace(location.host, 'teonistor.herokuapp.com')
          .replace('http:', 'https:');
      },

      afterHref () {
        return this.countdownGoing
          && ('in ' + this.seconds + ' seconds')
          || 'to enable full functionality';
      }
    },

    watch: {
      seconds () {
        if (this.backendPresent)
          this.seconds = -1;

        if (this.seconds > 0)
          setTimeout(() => this.seconds--, 1000);

        else if (this.seconds === 0)
          location.href = this.href;
      }
    },

    methods: {

    },

    mounted () {
      setTimeout(() => this.seconds--, 1000);
    }
  }
</script>
